import React, { useEffect, useState } from "react";
import matchSorter from 'match-sorter'
import axios from 'axios';
import { url } from '../../url'


// Import React Table
import ReactTable from "react-table";
import "react-table/react-table.css";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import { Line } from 'react-chartjs-2';

import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Divider,
  CardActions,
  useTheme,
  colors
} from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
const useStyles = makeStyles((theme) => ({
  root: {
    maxHeight: 1200,
    maxWidth:800
    
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },

}));

const urlBase = url + "medicion"
const Reporte = (props) => {
  const [total, setTotal] = useState(0)
  const [load, setLoad] = useState(false)
  const [temperatua, setTemp] = useState([]);
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const [chartData, setChartData] = useState("");
  const [data, setData] = useState([])
  const [filtro, setFiltro] = useState([])
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const getMedicion = async () => {
    const result = []
    const fil = []
    axios.get(urlBase + "/").then(
      response => {
        console.log(response.data, "los datos")
        response.data.map((dat) => {
          const createdAt = new Date(dat.Fecha);
          const createdDate = createdAt.toLocaleDateString('en-US');
          const createdTime = createdAt.toLocaleTimeString('en-US');

          result.push({ "profundidad": dat.Medicion["profundidad"], "turbidez": dat.Medicion["turbidez"], "temperatura": dat.Medicion["temp"], "fecha": createdDate, "hora": createdTime, "codBoya": dat.codBoya })
          fil.push(dat.codBoya)

        }
        )
        const dataArr = new Set(fil);

        setFiltro([...dataArr]);
        console.log(result, "el resultado es:")
        setData(result);
      }

    )
  }
  useEffect(() => {
    getMedicion()
  }, [])

  const formatData = (data) => {
    console.log("llego", data)
    let finalData = {
      labels: [],
      datasets: [
        {
          label: "Profundidad",
          data: [],
          type: 'line',
          backgroundColor: "rgb(255, 99, 132, 0.8)",
          borderColor: "rgba(255, 99, 132, 0.2)",
          fill: false
        },
        {
          label: "Temperatura",
          data: [],
          backgroundColor: "rgb(123, 12, 100, 0.8)",
          borderColor: "rgba(255, 99, 132, 0.2)",
          fill: false
        },
        {
          label: "Turbidez",
          data: [],
          backgroundColor: "rgb(10, 10, 212, 0.8)",
          borderColor: "rgba(255, 99, 132, 0.2)",
          fill: false
        }
      ]
    };


    let formato = []
    data.fecha.map((date => {
      const createdAt = new Date(date);
      const createdDate = createdAt.toLocaleDateString('en-US');
      const createdTime = createdAt.toLocaleTimeString('en-US');
      formato.push(createdTime)
    }))
    finalData.labels = formato;
    finalData.datasets[0].data = data.deep;
    finalData.datasets[1].data = data.temp;
    finalData.datasets[2].data = data.turbidez;

    console.log(load, "esta pailas")
    setChartData(finalData);

    setLoad(true)
    console.log(load, "ok depailas")
  }
  const Chart = async (id) => {
    let temp = [];
    let deep = [];
    let turbidez = [];
    let fecha = [];
    console.log("entro")
    let dataArr = {}
    let datos = []
    await axios.get(url + "medicion/")
      .then(res => {
        console.log(res.data.length, "holaassss", res.data);
        let size = 0;
        res.data.map((index) => {
          if (index.codBoya == id) {
            temp.push(index.Medicion["temp"]);
            deep.push(index.Medicion["profundidad"]);
            turbidez.push(index.Medicion["turbidez"]);
            fecha.push(index.Fecha)
            size++;

          }
        })
        let temp1 = [];
        let deep1 = [];
        let turbidez1 = [];
        let fecha1 = [];

        console.log(temp, "la temperatura es")
        setTemp(temp)
        setTotal(res.data.length)
        if (size > 30) {
          for (let x = size - 30; x < size; x++){
            temp1.push(temp[x])
            deep1.push(deep[x])
            turbidez1.push(turbidez[x]);
            fecha1.push(fecha[x])


          }
            dataArr = { "temp": temp1, "deep": deep1, "turbidez": turbidez1, "fecha": fecha1 }
        }else{
          dataArr = { "temp": temp, "deep": deep, "turbidez": turbidez, "fecha": fecha }
        }
        formatData(dataArr);








      })
      .catch(err => {
        console.log(err);
      })

  }
  useEffect(() => {

    Chart(1)

  }, []);

  return (
    <div>


      <Card className={classes.root}>
        <CardHeader
        title="Todas la Boyas"
        />
        <Divider />
        <CardContent>

          <ReactTable
            data={data}
            filterable
            defaultFilterMethod={(filter, row) =>
              String(row[filter.id]) === filter.value}
            columns={[
              {
                Header: "Medición",
                columns: [
                  {
                    Header: "Profundidad",
                    id: "profundidad",
                    accessor: d => d.profundidad,
                    filterMethod: (filter, rows) =>
                      matchSorter(rows, filter.value, { keys: ["profundidad"] }),
                    filterAll: true

                  },
                  {
                    Header: "Turbidez",
                    id: "turbidez",
                    accessor: d => d.turbidez,
                    filterMethod: (filter, rows) =>
                      matchSorter(rows, filter.value, { keys: ["turbidez"] }),
                    filterAll: true

                  },
                  {
                    Header: "Temperatura",
                    id: "temperatura",
                    accessor: d => d.temperatura,
                    filterMethod: (filter, rows) =>
                      matchSorter(rows, filter.value, { keys: ["temperatura"] }),
                    filterAll: true

                  },


                ]
              },
              {
                Header: "Fecha Hora",
                columns: [
                  {
                    Header: "Fecha",
                    id: "fecha",
                    accessor: d => d.fecha,
                    filterMethod: (filter, rows) =>
                      matchSorter(rows, filter.value, { keys: ["fecha"] }),
                    filterAll: true
                  },
                  {
                    Header: "Hora",
                    id: "hora",
                    accessor: d => d.hora,
                    filterMethod: (filter, rows) =>
                      matchSorter(rows, filter.value, { keys: ["hora"] }),
                    filterAll: true
                  },

                ]
              },
              {
                Header: "Boya",
                columns: [

                  {
                    Header: "Codigo",
                    id: "codBoya",
                    accessor: d => d.codBoya,
                    Filter: ({ filter, onChange }) =>
                      <select
                        onChange={event => {
                          onChange(event.target.value)
                          Chart(event.target.value)
                        }}
                        style={{ width: "100%" }}
                        value={filter ? filter.value : filtro}
                      >

                        {filtro.map((index) => (
                          <option value={index}>{index}</option>

                        ))}
                      </select>
                  },

                ]
              }
            ]}

            defaultPageSize={5}
            className="-striped -highlight"
          />

        </CardContent>
        <CardActions disableSpacing>
          <IconButton
            className={clsx(classes.expand, {
              [classes.expandOpen]: expanded,
            })}
            onClick={handleExpandClick}
            aria-expanded={expanded}
            aria-label="show more"
          >
            <ExpandMoreIcon />
          </IconButton>
        </CardActions>
        <Collapse in={expanded} timeout="auto" unmountOnExit>

          <CardContent>
            {load ?
              <Line
                data={chartData}
                options={{
                  responsive: true,
                  title: { text: "THICCNESS SCALE", display: true },
                  scales: {
                    xAxes: [{
                      title: "time",
                      type: 'time',
                      gridLines: {
                        lineWidth: 2
                      },
                      time: {
                        unit: "day",
                        unitStepSize: 1000,
                        displayFormats: {
                          millisecond: 'MMM DD',
                          second: 'MMM DD',
                          minute: 'MMM DD',
                          hour: 'MMM DD',
                          day: 'MMM DD',
                          week: 'MMM DD',
                          month: 'MMM DD',
                          quarter: 'MMM DD',
                          year: 'MMM DD',
                        }
                      }
                    }]
                  }
                }}
              /> : ("Leyendo")}


          </CardContent>
        </Collapse>
      </Card>

    </div>
  );

}

export default Reporte
